export default {
  components: {
  },
  data() {
    return {
			array: ['全部','极速', '精准', '收银台' ],
			array1: ['全部', '未知', '待支付', '支付中','支付成功','支付失败','结算中','结算成功','结算失败'],
			array2: ['全部', '等待执行', '执行中', '执行成功', '执行失败', '手动终止', '自动终止'],
      array3: ['全部'],
      time: [],
      index: 0,
      index1: 0,
      index2: 0,
      index3: 0,
      list: [],
      total: '',
      page: 1,
      size: 10,
      typelab: '',
      planstatus: '',
      startTime: '',
      endTime: '',
      finished: false,
      paystatus: ''
		};
  },
  onLoad() {
    this.getTime()
    this.getData()
  },
  methods: {
		bindPickerChange(type,e) {
      this.finished = false
      this.page = 1
      this.list = []
      this.total = ''
      let d = e.target.value
      this.list = []
      switch(type){
        case 0:
          this.index = d
          this.planstatus = ''
          this.index1 = 0
          this.index2 = 0
          this.planstatus = ''
          this.index ==0?this.typelab='':this.index==3?this.typelab=-1:this.typelab=this.index
          break;
        case 1:
          this.index1 = d
          this.index1==0?this.planstatus='':this.paystatus=this.index1-1
          break;
        case 2:
          this.index2 = d
          this.paystatus = ''
          this.index2==0?this.planstatus='':this.index2>=5?this.planstatus=this.index2:this.planstatus=this.index2-1
          break;
        case 3:
          this.index3 = d
          if(this.index3!=0){
            this.startTime = (new Date(this.time[this.index3-1].first)).getTime()/1000
            this.endTime = (new Date(this.time[this.index3-1].last)).getTime()/1000
          }
          break;
      }
			this.getData()
    },
    // 获取近半年的年月
    getTime(){
      var data = new Date();
			var year = data.getFullYear();
			data.setMonth(data.getMonth() + 1, 1); //获取到当前月份,设置月份
			for (var i = 0; i < 6; i++) {
			  data.setMonth(data.getMonth() - 1); //每次循环一次 月份值减1
			  var m = data.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        this.array3.push(data.getFullYear() + "-" + m);
        let myDate = new Date(data.getFullYear(), m, 0)
        this.time.push({
          first: data.getFullYear()+'-'+ m + '-' + '01' +  ' 00:00:00',
          last: data.getFullYear()+'-'+ m + '-' + myDate.getDate() +  ' 23:59:59'
        })
			}
    },
		getData(){
      let data = {
        typelab: this.typelab,
        planstatus: this.planstatus,
        startTime: this.startTime,
        endTime: this.endTime,
        paystatus: this.paystatus,
        page: this.page,
        size: this.size
      }
			this.$store.dispatch('getCashRecode',data).then(res=>{
        this.page++
        this.total = res.total
        this.list.push(...res.data)
        if(res.data.length<10){
          this.finished = true
        }
        this.loading = false
        uni.stopPullDownRefresh()
			}).catch(err=>{
        uni.stopPullDownRefresh()
      })
		}
  },
  onPullDownRefresh(){
    this.finished = false
    this.page = 1
    this.list = []
    this.getData()
  },
  onReachBottom(){
    if(!this.finished){
      this.getData()
    }
  }
};
